.fullInfo {
  padding-top: 5rem;
  padding-bottom: 5rem;
  margin-top: 10rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: #e0e4e6;
}

.fullInfo h3 {
  color: hsl(260, 8%, 14%);
  text-align: center;
}

.fullInfo_p {
  width: 15rem;
  margin-top: 0.8rem;
  font-size: 0.7rem;
  text-align: center;
  color: hsl(0, 0%, 75%);
}

.frame_position {
  display: flex;
  flex-direction: column;
}

.frame {
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-top: 3rem;
  padding-bottom: 1rem;
  background-color: hsl(0, 0%, 100%);
  margin-top: 6rem;
  width: 15rem;
  min-height: 15rem;
  max-height: auto;
  border-radius: 5px;
}

.frame h4 {
  color: hsl(257, 27%, 26%);
}

.frame p {
  width: 12rem;
}

.picto {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 12.5rem;
  background-color: hsl(257, 27%, 26%);
  height: 5rem;
  width: 5rem;
  border-radius: 50%;
}

.picto img {
  width: 3rem;
  height: 3rem;
}

.vLine {
  height: 50rem;
  bottom: 20rem;
  position: absolute;
  border: 4px solid hsl(180, 66%, 49%);
  background-color: hsl(180, 66%, 49%);
}

@media screen and (min-width: 56.31rem) {
  .fullInfo h3 {
    font-size: 2rem;
  }
  .fullInfo_p {
    width: 30rem;
    font-size: 1rem;
  }
  .frame_position {
    width: 80vw;
    flex-direction: row;
    justify-content: space-between;
  }
  .frame {
    width: 30%;
  }
  .frame:nth-child(1) {
    top: -2rem;
  }
  .frame:nth-child(3) {
    top: 2rem;
  }
  .picto {
    bottom: 15rem;
  }
  .vLine {
    height: 0;
    width: 65vw;
    bottom: 32rem;
  }
}