@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

#root {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}


.mobMenu {
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 4rem;
    width: 16rem;
    height: 21rem;
    background-color: hsl(257, 27%, 26%);
    border-radius: 10px;
    font-weight: 700;
}

.mobMenu ul {
    list-style-type: none;
}

.mobMenu li {
    margin: 1.5rem 0 1.5rem 0;
    text-align: center;
}

.hr {
    width: 14rem;
    height: 1px;
    background-color: hsl(257, 7%, 63%)
}

.sign {
    width: 14rem;
    height: 2.5rem;
    line-height: 2.5rem;
    border: 2px solid hsl(180, 66%, 49%);
    background-color: hsl(180, 66%, 49%);
    border-radius: 2rem;
}

@media screen and (min-width: 56.31rem) {
    .mobMenu {
        display: none;
    }
}
.desktopMenu {
    grid-area: 1/2/2/7;
    display: flex;
    align-items: center;
}

.desktopMenu_pages li {
    display: inline;
    padding-right: 3rem;
    font-size: 0.8rem;
    color: grey;
    height: 2rem;
    line-height: 2rem;
}

.desktopMenu_login {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 3rem;
}

.desktopMenu_login li {
    display: inline;
    font-size: 0.8rem;
    color: grey;
    margin-right: 3rem;
}

.desktopSign {
    background-color: hsl(180, 66%, 49%);
    border-radius: 3rem;
    width: 6rem;
    height: 2rem;
    line-height: 2rem;
    text-align: center;
    color: white !important;
}

@media screen and (max-width: 56.25rem) {
    .desktopMenu {
        display: none;
    }
}
header {
    display: flex;
    justify-content: center;
    width: 90vw;
}

.siteNav {
    display: grid;
    width: 80vw;
    height: 25rem;
    grid-template-columns: 7rem auto auto auto auto auto;
    grid-template-rows: 3.5rem 1fr;
    background-image: url(/URLShorteningAPI/static/media/illustration-working.c9b5ef54.svg);
    background-repeat: no-repeat;
    background-size: 380px auto;
    background-position: center;
}

.logo {
    margin-top: 1.3rem;
    width: 80px;
    height: 20px;
}

.menuBar {
    grid-area: 1/6/2/7;
    color: rgb(134, 134, 134);
    justify-self: flex-end;
    margin-top: 1.5rem;
    margin-right: 1rem;
    width: 18px;
    transition: 0.5s;
}

.menuBar:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

@media screen and (min-width: 56.31rem) {
    .siteNav {
        background-position: right;
        background-size: 420px auto;
    }
    .menuBar {
        display: none;
    }
}
.head {
    display: flex;
    justify-content: center;
    width: 80vw;
}

.head h2 {
    font-size: 2rem;
    width: 15rem;
    text-align: center;
    color: hsl(257, 27%, 26%);
}

@media screen and (min-width: 56.31rem) {
    .head {
        justify-content: flex-start;
    }

    .head h2 {
        font-size: 3rem;
        width: 25rem;
        text-align: left;
    }
}
.siteInfo {
    position: relative;
    margin-top: 1rem;
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
    width: 80vw;
}

.siteInfo p {
    color: hsl(0, 0%, 57%);
    font-size: 0.85rem;
    width: 15rem;
    text-align: center;
}

@media screen and (min-width: 56.31rem) {
    .siteInfo {
        justify-content: flex-start;
    }

    .siteInfo p {
        font-size: 1rem;
        width: 23.7rem;
        text-align: left;
    }
  }
.mainButton {
  display: flex;
  justify-content: center;
  align-items: center;
}

.mainButton button {
  color: white;
  font-weight: 700;
  background-color: hsl(180, 66%, 49%);
  width: 9rem;
  height: 2.5rem;
  border: none;
  border-radius: 2rem;
}
.shorten {
  z-index: 1;
  display: flex;
  flex-direction: column;
  height: 8rem;
  background-color: hsl(257, 27%, 26%);
  border-radius: 10px;
  position: absolute;
  top: 21rem;
  background-image: url(/URLShorteningAPI/static/media/bg-shorten-mobile.41c34b33.svg);
  background-repeat: no-repeat;
  background-size: 11rem 5rem;
  background-position: top right;
}

.shorten form {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 15rem;
  height: 100%;
  padding: 1rem 0 1rem 0;
}

.shorten input {
  width: 13rem;
  height: 2rem;
  border-radius: 3px;
  outline: none;
  padding-left: 0.3rem;
}

.linkAlert {
  color: hsl(0, 100%, 72%);
  font-size: 0.8rem;
  width: 13rem;
  font-style: italic;
  padding: 0.2rem 0 0 0;
}

.shorten input::-webkit-input-placeholder {
  color: hsl(0, 0%, 73%);
}

.shorten input:-ms-input-placeholder {
  color: hsl(0, 0%, 73%);
}

.shorten input::placeholder {
  color: hsl(0, 0%, 73%);
}

.shorten button {
  margin-top: 0.5rem;
  width: 13rem;
  height: 2rem;
  border-radius: 10px;
  border: none;
  background-color: hsl(180, 66%, 49%);
  color: white;
}

@media screen and (min-width: 56.31rem) {
  .shorten {
    top: 23.5rem;
    width: 80vw;
    align-items: center;
    background-image: url(/URLShorteningAPI/static/media/bg-shorten-desktop.0d9742f4.svg);
    background-size: 100% 100%;
    background-position: top right;
  }

  .shorten form {
    flex-direction: row;
    width: 90%;
  }

  .shorten input {
    width: 100%;
    height: 3rem;
  }

  .linkAlert {
    position: absolute;
    left: 0;
    top: 5.5rem;
  }

  .shorten button {
    align-items: flex-start;
    margin:  0 0 0 1rem;
    width: 12rem;
    height: 3rem;
  }
}
.fullInfo {
  padding-top: 5rem;
  padding-bottom: 5rem;
  margin-top: 10rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: #e0e4e6;
}

.fullInfo h3 {
  color: hsl(260, 8%, 14%);
  text-align: center;
}

.fullInfo_p {
  width: 15rem;
  margin-top: 0.8rem;
  font-size: 0.7rem;
  text-align: center;
  color: hsl(0, 0%, 75%);
}

.frame_position {
  display: flex;
  flex-direction: column;
}

.frame {
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-top: 3rem;
  padding-bottom: 1rem;
  background-color: hsl(0, 0%, 100%);
  margin-top: 6rem;
  width: 15rem;
  min-height: 15rem;
  max-height: auto;
  border-radius: 5px;
}

.frame h4 {
  color: hsl(257, 27%, 26%);
}

.frame p {
  width: 12rem;
}

.picto {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 12.5rem;
  background-color: hsl(257, 27%, 26%);
  height: 5rem;
  width: 5rem;
  border-radius: 50%;
}

.picto img {
  width: 3rem;
  height: 3rem;
}

.vLine {
  height: 50rem;
  bottom: 20rem;
  position: absolute;
  border: 4px solid hsl(180, 66%, 49%);
  background-color: hsl(180, 66%, 49%);
}

@media screen and (min-width: 56.31rem) {
  .fullInfo h3 {
    font-size: 2rem;
  }
  .fullInfo_p {
    width: 30rem;
    font-size: 1rem;
  }
  .frame_position {
    width: 80vw;
    flex-direction: row;
    justify-content: space-between;
  }
  .frame {
    width: 30%;
  }
  .frame:nth-child(1) {
    top: -2rem;
  }
  .frame:nth-child(3) {
    top: 2rem;
  }
  .picto {
    bottom: 15rem;
  }
  .vLine {
    height: 0;
    width: 65vw;
    bottom: 32rem;
  }
}
.buttonCopy {
    height: 2rem;
    width: 13rem;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    background-color: hsl(180, 66%, 49%);
    border: none;
    border-radius: 5px;
    color: white;
}

.buttonCopy:hover {
    background-color: hsl(180, 66%, 44%);
}

.buttonCopy:active {
    box-shadow: inset 0px 0px 50px -2px hsl(180, 66%, 49%);
}

@media screen and (min-width: 56.31rem) {
    .buttonCopy {
        margin: 0 1rem 0 0;
    }
}
.linksHolder {
    width: 15rem;
    height: auto;
    margin: 0 0 2rem 0;
}

.linksBlock {
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    align-items: center;
    height: auto;
    background-color: white;
    border-radius: 5px;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.pLink {
    width: 100%;
    text-align: center;
    color: hsl(0, 0%, 0%);
}

.hr {
    width: 100%;
    background-color: hsl(0, 0%, 88%);
}

.aLink {
    color: hsl(180, 66%, 49%);
}

.linksFit {
    margin: 0.5rem 0 0.5rem 0;
    font-size: 1rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    padding: 0 5px 0 5px;
}

@media screen and (min-width: 56.31rem) {
    .linksHolder {
        width: 85vw;
    }
    .linksBlock {
        flex-direction: row;
        height: 3rem;
    }
    .pLink {
        width: 100%;
        text-align: left;
        padding: 0 0 0 1rem;
    }
    .hr {
        display: none;
    }
    .aLink {
        width: 100%;
        text-align: right;
        padding: 0 1rem 0 0;
    }
}
.back {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 15rem;
  background-color: hsl(257, 27%, 26%);
  color: white;
  background-image: url(/URLShorteningAPI/static/media/bg-boost-mobile.4b04bd89.svg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.back h3 {
  margin-bottom: 1rem;
}

.startedBtn_position {
  display: flex;
  justify-content: center;
}

@media screen and (min-width: 56.31rem) {
  .back {
    background-image: url(/URLShorteningAPI/static/media/bg-boost-desktop.772154c9.svg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
  .back h3 {
    font-size: 2rem;
  }
}
.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  position: relative;
}

.btn_position {
  width: 80vw;
}

@media screen and (min-width: 56.31rem) {
  .main {
    top: -20rem;
    margin-bottom: -20rem;
  }

  .btn_position {
    display: flex;
    justify-self: flex-start;
    align-items: flex-start;
  }
}
footer {
    background-color: hsl(260, 8%, 14%);
    width: 100%;
    display: flex;
    justify-content: center;
}

.footer_content {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 0 2rem 0;
}


.position {
    display: flex;
    flex-direction: column;
    align-items: center;
}

footer h2 {
    color: white;
    margin-top: 2rem;
}

footer h4 {
    color: white;
    margin-top: 2rem;
}

footer ul {
    margin-top: 1rem;
    list-style-type: none;
}

footer li {
    font-size: 1rem;
    color: grey;
    text-align: center;
    margin-top: 0.3rem;
}

footer button {
    border: none;
    background-color: transparent;
    color: grey;
    font-size: 1rem;
}

footer button:hover {
    cursor: pointer;
}

.social {
    display: flex;
    justify-content: space-between;
    width: 12rem;
    margin: 2rem 0 2rem 0;
}

.social img {
    width: 2rem;
}

@media screen and (min-width: 56.25rem) {
    .footer_content {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
      width: 90vw;
    }

    footer h2 {
        width: 10rem;
    }

    .position {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    footer li {
        text-align: left;
    }

    .social img {
        width: 1.5rem;
    }
  }


