.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  position: relative;
}

.btn_position {
  width: 80vw;
}

@media screen and (min-width: 56.31rem) {
  .main {
    top: -20rem;
    margin-bottom: -20rem;
  }

  .btn_position {
    display: flex;
    justify-self: flex-start;
    align-items: flex-start;
  }
}